exports.push([module.id, "\n#transaction-page[data-v-3fcd2084] {\n    height: 100%;\n    background: url(/assets/bg.coinally.png?611e37f) 50%;\n    background-size: auto auto;\n    background-size: cover;\n    padding-top: 1px;\n}\n", "", {"version":3,"sources":["C:/Users/chido/Desktop/Projects/Repos/afrivelle-projects/coinally-combined/src/components/newTransaction/src/components/newTransaction/index.vue"],"names":[],"mappings":";AAoDA;IACA,aAAA;IACA,sDAAA;IACA,2BAAA;IACA,uBAAA;IACA,iBAAA;CACA","file":"index.vue","sourcesContent":["<template>\r\n  <div id=\"transaction-page\">\r\n    <div class=\"modal-component exchange-modal verified in\">\r\n      <div class=\"modal-dialog\">\r\n        <div class=\"modal-content\">\r\n          <div class=\"modal-header clearfix\">\r\n            <span class=\"modal-title\">\r\n              Verified\r\n            </span>\r\n            <button\r\n              type=\"button\"\r\n              data-dismiss=\"modal\"\r\n              @click=\"goBack\"/>\r\n          </div>\r\n          <div class=\"modal-body\">\r\n            <div class=\"modal-body__container\">\r\n              <router-view/>\r\n            </div>\r\n            <div class=\"order-details\">\r\n              <div\r\n                v-if=\"depositAsset && receiptAsset && conversionRate\"\r\n                class=\"transaction__type\">\r\n                <img\r\n                  :src=\"global.apiRootUrl + '/' + depositAsset.imagePath\"\r\n                  :alt=\"depositAsset.name\"> \r\n                <img\r\n                  :src=\"global.apiRootUrl + '/' + receiptAsset.imagePath\"\r\n                  :alt=\"receiptAsset.name\">\r\n              </div>\r\n              <div\r\n                v-if=\"depositAsset && receiptAsset && conversionRate\"\r\n                class=\"expected-amount\">\r\n                <div class=\"amount\">\r\n                  {{ `${amount} ${depositAsset.code.toUpperCase()} (${receiptAmount}${receiptAsset.code.toUpperCase()})` }}\r\n                </div>\r\n                <div class=\"exchange-rate\">\r\n                  {{ `1 ${depositAsset.code.toUpperCase()} = ${conversionRate.toFixed(8).replace(/(?:\\.)?0+$/, \"\")}${receiptAsset.code.toUpperCase()}` }}\r\n                </div>\r\n              </div>\r\n\r\n            </div>\r\n          </div>\r\n\r\n        </div>\r\n      </div>\r\n\r\n    </div>\r\n\r\n  </div>\r\n</template>\r\n\r\n<style scoped>\r\n    #transaction-page {\r\n        height: 100%;\r\n        background: url(/assets/bg.coinally.png?611e37f) 50%;\r\n        background-size: auto auto;\r\n        background-size: cover;\r\n        padding-top: 1px;\r\n    }\r\n</style>\r\n\r\n<script>\r\nimport socketIoClient from 'socket.io-client';\r\n\r\nexport default {\r\n  data() {\r\n    return {\r\n      depositAsset: {\r\n\r\n      },\r\n      receiptAsset: {\r\n\r\n      },\r\n      amount: 0,\r\n      conversionRate: 0,\r\n      socket: null,\r\n      transaction: {},\r\n    };\r\n  },\r\n  computed: {\r\n    isValidated() {\r\n      return !!(this.depositAsset && this.receiptAsset);\r\n    },\r\n    receiptAmount() {\r\n      return this.amount * this.conversionRate;\r\n    },\r\n  },\r\n  mounted() {\r\n    this.initializeProps();\r\n    this.initializeSocket();\r\n  },\r\n  inject: ['global'],\r\n  methods: {\r\n    goBack() {\r\n      this.$router.go(-1);\r\n    },\r\n    initializeSocket() {\r\n      if (!this.socket) {\r\n        const query = {\r\n          from: this.depositAsset.code,\r\n          to: this.receiptAsset.code,\r\n        };\r\n        const socket = socketIoClient(`${this.global.apiRootUrl}/rates`, {\r\n          reconnectionDelay: 10000,\r\n          query,\r\n        });\r\n\r\n        socket.on('exception', err => console.log('exception: ', err));\r\n        socket.on('error', (...args) => console.log('error event: ', ...args));\r\n        socket.on('connect_timeout', (...args) => console.log('timeout: ', ...args));\r\n        socket.on('new_rate', (rate) => {\r\n          this.conversionRate = rate;\r\n\r\n          // this.transaction = sessionStorage.getItem('transaction');\r\n          // this.transaction.rate = rate;\r\n\r\n          // sessionStorage.setItem('transaction', JSON.stringify(this.transaction));\r\n        });\r\n        this.socket = socket;\r\n      }\r\n    },\r\n    initializeProps() {\r\n      const sessionData = sessionStorage.getItem('transaction');\r\n\r\n      if (!sessionData) {\r\n        return this.$router.push('/');\r\n      }\r\n\r\n      this.transaction = JSON.parse(sessionData);\r\n\r\n      this.depositAsset = this.transaction.depositAsset;\r\n      this.receiptAsset = this.transaction.receiptAsset;\r\n      this.amount = this.transaction.amount;\r\n      this.conversionRate = this.transaction.rate;\r\n\r\n      return true;\r\n    },\r\n  },\r\n};\r\n</script>\r\n"],"sourceRoot":""}]);

